import Vue from "vue";
import VueRouter from "vue-router";
import MainView from "../views/MainView.vue";
import LoginView from "../views/LoginView.vue";
import MypageView from "../views/MypageView.vue";
import GoogleLoginCallBack from "../views/GoogleLoginCallBack.vue";
import KakaoLoginCallBack from "../views/KakaoLoginCallBack.vue";
import SettingView from "../views/SettingView.vue";
import ErrorView from "../views/ErrorView.vue";
import TermsofUse from "../views/TermsofUse.vue";

// import userStore from "@/store/modules/userStore";

Vue.use(VueRouter);

const routes = [
  {
    path: "/main/:pageid",
    name: "MainView",
    component: MainView,
  },
  {
    path: "/",
    name: "LoginView",
    component: LoginView,
  },
  {
    path: "/mypage",
    name: "MypageView",
    component: MypageView,
  },
  {
    path: "/oauth/kakao/callback",
    name: "kakaoLoginCallBack",
    component: KakaoLoginCallBack,
  },
  {
    path: "/oauth/google/callback",
    name: "googleLoginCallBack",
    component: GoogleLoginCallBack,
  },
  {
    path: "/settings",
    name: "SettingView",
    component: SettingView,
  },
  {
    path: "/terms",
    name: "TermsofUse",
    component: TermsofUse,
  },
  {
    path: "/error",
    name: "ErrorView",
    component: ErrorView,
  },

];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

